.App {
  text-align: center;
  background-color: #2f3136;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.profile-pic {
  height: 40vmin;
  pointer-events: none;
  border-radius: 500px;
}

@media (prefers-reduced-motion: no-preference) {
  .profile-pic {
    animation: profile-pic 2s cubic-bezier(0.51, 0.35, 0.17, 1.04) infinite
  }
}

@keyframes profile-pic {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}